*,
:after,
:before {
	background-repeat: no-repeat;
	box-sizing: border-box;
}

:after,
:before {
	text-decoration: inherit;
	vertical-align: inherit
}

* {
	margin: 0;
	padding: 0
}

hr {
	transition: border-color .2s;
	height: 0;
	overflow: visible;
	margin: .75rem 0;
	opacity: 1;
	border: none;
	border-top: 1px solid var(--outline-colour);
}

chip-sidebaritem {
	display: flex;
	align-items: center;
	position: relative;
	gap: var(--block-size-sm);
}

details,
main,
chip-input,
chip-card,
chip-grid,
chip-cardheader,
chip-list,
chip-listitem,
chip-progressbar,
chip-progressbar progress,
chip-carousel,
chip-cardfooter,
chip-infogroup,
chip-infogroupvalue,
chip-dropdown,
chip-dropdowndivider,
chip-dropdowngroup,
chip-callout,
chip-sortablelist,
chip-sortablelistitem,
chip-tree,
chip-contextmenu,
chip-menuitem,
chip-menudivider,
chip-accordion,
chip-actionbar,
chip-accordionitem,
chip-tab,
chip-form,
chip-emptyprompt,
chip-iconpicker,
chip-buttongroup,
chip-select,
chip-option,
chip-colourpicker,
chip-badge,
chip-label,
chip-codeblock,
chip-header,
chip-breadcrumb,
chip-range,
chip-progress,
chip-progressitem,
chip-text {
	display: block;
}

chip-input:not(:defined),
chip-text:not(:defined),
chip-list:not(:defined),
chip-listitem:not(:defined),
chip-progress:not(:defined),
chip-grid:not(:defined),
chip-carousel:not(:defined),
chip-carouselslide:not(:defined),
chip-beacon:not(:defined),
chip-card:not(:defined),
chip-select:not(:defined),
chip-option:not(:defined),
chip-link:not(:defined),
chip-infogroup:not(:defined),
chip-infogroupvalue:not(:defined),
chip-dropdown:not(:defined),
chip-dropdowndivider:not(:defined),
chip-dropdowngroup:not(:defined),
chip-callout:not(:defined),
chip-sortablelist:not(:defined),
chip-sortablelistitem:not(:defined),
chip-tree:not(:defined),
chip-treeitem:not(:defined),
chip-contextmenu:not(:defined),
chip-menuitem:not(:defined),
chip-menudivider:not(:defined),
chip-accordion:not(:defined),
chip-accordionitem:not(:defined),
chip-tab:not(:defined),
chip-form:not(:defined),
chip-emptyprompt:not(:defined),
chip-iconpicker:not(:defined),
chip-buttongroup:not(:defined),
chip-colourpicker:not(:defined),
chip-badge:not(:defined),
chip-label:not(:defined),
chip-codeblock:not(:defined),
chip-header:not(:defined),
chip-breadcrumb:not(:defined),
chip-range:not(:defined),
chip-progress:not(:defined),
chip-progressitem:not(:defined),
chip-button:not(:defined),
chip-dropdownbutton:not(:defined),
chip-splitbutton:not(:defined),
chip-frame:not(:defined),
chip-framecontent:not(:defined),
chip-framesidebar:not(:defined),
chip-sidebaritem:not(:defined),
chip-sidebarheader:not(:defined) {
	opacity: 0;
}

chip-link,
chip-dateformat {
	display: inline;
}

chip-carousel {
	position: relative;
}

chip-carouselslide {
	height: 100%;
	width: 100%;
}

chip-carouselslide[active] {
	display: block;
}

chip-label[size="body"] {
	font-size: var(--body-font-size);
}

chip-progressbar {
	--progress-colour: var(--theme-colour);
	--progress-radius: var(--strong-border-radius);
	--progress-background: var(--outline-colour);
	--progress-height: var(--block-size-md);
}

chip-progressbar progress {
	width: 100%;
	border-width: 0;
	border-color: rgba(0, 0, 0, 0);
	border-radius: var(--progress-radius);
	background: var(--progress-background);
	height: var(--progress-height);
}

chip-progressbar progress::-webkit-progress-bar {
	border-radius: var(--progress-radius);
	background: var(--progress-background);
}

chip-progressbar progress::-webkit-progress-value {
	border-radius: var(--progress-radius) 0 0 var(--progress-radius);
}

chip-progressbar[variation="success"] {
	--progress-colour: var(--success-colour);
}

chip-progressbar[variation="warning"],
chip-progressbar.progress--low-warning,
chip-progressbar.progress--high-warning {
	--progress-colour: var(--warning-colour);
}

chip-progressbar[variation="danger"],
chip-progressbar.progress--low,
chip-progressbar.progress--high {
	--progress-colour: var(--danger-colour);
}

chip-progressbar[variation="info"] {
	--progress-colour: var(--info-colour);
}

chip-progressbar[variation="secondary"] {
	--progress-colour: var(--secondary-colour);
}

chip-progressbar progress::-moz-progress-bar {
	background: var(--progress-colour);
}

chip-progressbar progress::-webkit-progress-value {
	background: var(--progress-colour);
}

chip-progressbar[indeterminate] {
	position: relative;
}

chip-progressbar[indeterminate] progress {
	display: none;
}

chip-progressbar[indeterminate] .progress-bar {
	width: 100%;
	height: var(--progress-height);
	background: var(--progress-background);
	border-radius: var(--progress-radius);
	overflow: hidden;
	position: relative;
}

chip-progressbar[indeterminate] .progress-fill {
	width: 50%;
	height: 100%;
	background: var(--progress-colour);
	border-radius: var(--progress-radius);
	position: absolute;
	top: 0;
	left: 0;
	animation: indeterminate 1.25s linear infinite;
}

@keyframes indeterminate {
	0% {
		left: -50%;
		width: 50%;
	}

	50% {
		left: 25%;
		width: 50%;
	}

	100% {
		left: 100%;
		width: 50%;
	}
}

chip-button.carousel__action--previous[variation="none"],
chip-button.carousel__action--next[variation="none"] {
	--btn-colour: var(--body-colour-faded);
	--btn-hover-colour: var(--body-colour);
	position: absolute;
	top: 0;
	bottom: 0;
	z-index: 1;
	--btn-padding-x: var(--block-size-xl);
	--btn-font-size: var(--body-font-size-lg);
}

.carousel__action--next {
	right: 0;
}

.carousel__action--previous .btn--base,
.carousel__action--next .btn--base {
	margin: auto;
}

.carousel__slide-list,
.carousel__slide-list>li {
	display: flex;
}

.carousel__slide-list>li {
	scroll-snap-align: start;
	scroll-behavior: smooth;
	width: 100%;
	flex-shrink: 0;
}

.carousel__slide-list {
	overflow: hidden;
	scroll-snap-type: x mandatory;
}

.carousel__slide-container {
	width: 100%;
	height: 100%;
}

chip-breadcrumb {
	--breadcrumb-separator: "/";
	--breadcrumb-separator-colour: var(--body-colour-faded);
	--breadcrumb-colour: var(--body-colour-faded);
	--breadcrumb-hover-colour: var(--theme-colour);
	--breadcrumb-font-weight: var(--font-weight-medium);
}

chip-tab {
	display: flex;
	position: relative;
}

chip-textarea,
chip-tabgroup {
	display: flex;
	flex-direction: column;
}

chip-treeitem {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	padding: var(--tree-item-spacing) calc(var(--tree-item-spacing) + .25em);
	padding-right: var(--tree-item-spacing);
}

chip-tabgroup[orientation="vertical"]:not([direction="right"]) {
	flex-direction: row;
}

chip-tabgroup[orientation="vertical"][direction="right"] {
	flex-direction: row-reverse;
}

chip-button,
chip-dropdownbutton,
chip-splitbutton {
	display: inline-flex;
	--btn-font-family: var(--body-font-family);
	--btn-font-weight: var(--font-weight-medium);
	--btn-border-radius: .4rem;
	--btn-disabled-colour: var(--btn-colour);
	--btn-disabled-background-colour: var(--btn-background-colour);
	--btn-disabled-border-colour: var(--btn-border-colour);
	--btn-colour: var(--body-colour);
	--btn-background-colour: rgba(0, 0, 0, 0);
	--btn-border-colour: var(--btn-background-colour);
	--btn-box-shadow: none;
	--btn-focus-colour: var(--btn-colour);
	--btn-focus-background-colour: var(--btn-background-colour);
	--btn-focus-border-colour: var(--btn-border-colour);
	--btn-focus-box-shadow: var(--btn-box-shadow);
	--btn-hover-colour: var(--btn-colour);
	--btn-hover-background-colour: var(--btn-background-colour);
	--btn-hover-border-colour: var(--btn-hover-background-colour);
	--btn-hover-box-shadow: var(--btn-box-shadow);
	--btn-active-colour: var(--btn-hover-colour);
	--btn-active-background-colour: var(--btn-hover-background-colour);
	--btn-active-border-colour: var(--btn-hover-border-colour);
	--btn-active-box-shadow: var(--btn-hover-box-shadow);
	--btn-font-size: var(--body-font-size);
	--btn-line-height: 1.4286rem;
	--btn-padding-y: 0;
	--btn-padding-x: var(--block-size-md);
	--link-hover-decoration: none;
	--btn-horizontal-alignment: center;
	--btn-vertical-alignment: center;
}

chip-dropdown[limit-width]>.input-field ul {
	max-width: 100%;
}

chip-splitbutton {
	height: fit-content;
}

chip-dropdownitem,
chip-dropdownheader,
chip-breadcrumbitem {
	display: flex;
	align-items: center;
	text-align: start;
}

chip-breadcrumbitem {
	color: var(--breadcrumb-colour);
}

chip-infogroup {
	--group-header-border-bottom: 1px solid var(--outline-colour);
	--group-row-spacing: var(--block-size-base);
	--group-value-colour: var(--body-colour);
	--group-spacing: var(--block-size-xl);
	--group-header-padding: 0 0 var(--block-size-xxs) 0;
	--group-header-margin: 0 0 var(--block-size-md) 0;
}

chip-infogroup+chip-infogroup {
	margin-top: var(--group-spacing);
}

chip-dropdown:not(chip-dropdownitem>chip-dropdown),
chip-dropdown>.input-field {
	position: relative;
}

chip-dropdownitem>chip-icon:first-child {
	text-align: center;
	min-width: 1.25em;
}

chip-tabgroup {
	--tab-colour: var(--theme-colour);
	--tab-colour-foreground: var(--theme-colour-foreground);
	--tab-gap: var(--block-size-lg);
	--tab-padding: 0;
	--tab-border: none;
	--tab-header-font-weight: var(--font-weight-semibold);
	--tab-header-border-width: 2px;
	--tab-header-colour: var(--body-colour-faded);
	--tab-header-background-colour: rgba(0, 0, 0, 0);
	--tab-animation: fadeIn .5s;
	--selected-tab-header-colour: var(--body-colour);
	gap: var(--tab-gap);
}

chip-tabgroup[scroll] .tab-group__tab {
	overflow: auto;
}

chip-dropdownitem>chip-icon:first-child,
chip-dropdown>div>button>span>chip-icon,
chip-badge:not([size], [badge-style="icon"])>chip-icon:first-child,
chip-badge:not([badge-style="icon"])[size="lg"]>chip-icon:first-child {
	margin-right: var(--block-size-sm);
}

chip-badge:not([size])>chip-icon:first-child,
chip-badge:not([badge-style="icon"])[size="lg"]>chip-icon:first-child {
	font-size: var(--body-font-size-xs);
}

chip-badge:not([badge-style="icon"])[size="sm"]>chip-icon:first-child,
chip-badge:not([badge-style="icon"])[size="xs"]>chip-icon:first-child {
	margin-right: var(--block-size-xs);
}

chip-badge[badge-style="icon"]>chip-icon:first-child {
	min-width: 1.25em;
}

chip-badge[badge-style="pill"] {
	border-radius: 1000px;
}

chip-badge[badge-style="icon"] {
	border-radius: 1000px;
	aspect-ratio: 1;
	padding: 0 calc(var(--block-size-xs) * 1.5);
}

chip-buttongroup:not([spacing]) li:not(:first-child) .btn--base {
	border-top-left-radius: 0;
	border-bottom-left-radius: 0;
}

chip-buttongroup:not([spacing]) li:not(:last-child) .btn--base {
	border-top-right-radius: 0;
	border-bottom-right-radius: 0;
}

chip-buttongroup {
	--button-group-gap: 0;
}

chip-buttongroup[spacing="xxs"] ul {
	--button-group-gap: var(--block-size-xxs);
}

chip-buttongroup[spacing="xs"] ul {
	--button-group-gap: var(--block-size-xs);
}

chip-buttongroup[spacing="sm"] ul {
	--button-group-gap: var(--block-size-sm);
}

chip-buttongroup[spacing="md"] ul {
	--button-group-gap: var(--block-size-md);
}

chip-buttongroup[spacing="base"] ul {
	--button-group-gap: var(--block-size-base);
}

chip-buttongroup[spacing="standard"] ul {
	--button-group-gap: var(--block-size-standard);
}

chip-buttongroup[spacing="lg"] ul {
	--button-group-gap: var(--block-size-lg);
}

chip-buttongroup[spacing="xl"] ul {
	--button-group-gap: var(--block-size-xl);
}

chip-buttongroup[spacing="xxl"] ul {
	--button-group-gap: var(--block-size-xxl);
}

chip-buttongroup[spacing="xxxl"] ul {
	--button-group-gap: var(--block-size-xxxl);
}

chip-buttongroup[spacing="xxxxl"] ul {
	--button-group-gap: var(--block-size-xxxxl);
}

chip-dropdown:not(chip-splitbutton chip-dropdown, [form-width]),
chip-input:not([form-width]),
chip-select:not([form-width]) {
	width: 100%;
}

chip-buttongroup ul {
	display: flex;
	gap: var(--button-group-gap);
}

chip-button>button,
chip-button>a,
chip-dropdownbutton>chip-dropdown>div>button {
	width: 100%;
	height: 100%;
}

chip-buttongroup button,
chip-buttongroup chip-button {
	block-size: 100% !important;
}

chip-codeblock {
	background: var(--code-block-background-colour);
	position: relative;
	padding: var(--block-size-lg);
	transition: background .2s, border-color .2s;
	height: fit-content;
}

chip-codeblock>.row>.col:first-child {
	overflow-x: auto;
}

[suffix-icon] .btn--base:not(.btn--icon, .btn--vertical)>chip-icon:last-child {
	margin-left: var(--block-size-sm);
}

.btn--base:not(.btn--icon, .btn--vertical, .mega-menu-item, [suffix-icon] .btn--base, .tab__close-button .btn--base, chip-tab:not([label])>.btn--base)>chip-icon:first-child,
chip-breadcrumbitem chip-icon:first-child {
	margin-right: var(--btn-icon-gap, var(--block-size-sm));
}

chip-breadcrumbitem chip-icon:not(chip-breadcrumbitem:hover i) {
	color: var(--thumb-colour);
}

.btn--base:not(.btn--icon, .btn--vertical)>.beacon:first-child {
	margin-right: var(--block-size-md);
}

.btn--vertical:not(.btn--icon)>chip-icon:first-child {
	margin-bottom: var(--block-size-sm);
}

chip-colourpicker:not([presets]) .dropdown__list {
	padding: var(--block-size-standard) var(--block-size-sm);
}

chip-colourpicker:not([presets]) .dropdown__list>li:first-child {
	padding: 0 var(--block-size-md);
}

chip-colourpicker canvas {
	height: 200px;
	width: 200px;
	border-radius: var(--standard-border-radius);
}

chip-infovalue[max-height] .information__value {
	overflow: auto;
}

chip-infovalue[max-height="xs"] .information__value {
	max-height: calc(var(--block-size-xs) * 5);
}

chip-infovalue[max-height="sm"] .information__value {
	max-height: calc(var(--block-size-sm) * 6);
}

chip-infovalue[max-height="md"] .information__value {
	max-height: calc(var(--block-size-md) * 6);
}

chip-infovalue[max-height="lg"] .information__value {
	max-height: calc(var(--block-size-lg) * 5);
}

chip-infovalue[max-height="xl"] .information__value {
	max-height: calc(var(--block-size-xl) * 5);
}

chip-colourpicker[value][presets] chip-dropdown>.input-field>button>chip-icon {
	color: var(--theme-colour);
}

.colour-picker__preset[data-colour=""] {
	--theme-colour: rgba(0, 0, 0, 0);
	--theme-colour-foreground: var(--body-colour);
	font-size: 25px;
}

chip-colourpicker[presets] li>button {
	padding: 0;
	transition: scale .2s;
}

chip-colourpicker[presets] li>button:hover {
	scale: 1.1;
}

chip-colourpicker[presets] .dropdown__list {
	padding: var(--block-size-base);
	overflow: hidden;
	gap: var(--block-size-sm);
	grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
}

.spectrum-cursor,
.hue-cursor {
	width: 30px;
	aspect-ratio: 1;
	border-radius: 50%;
	border: 2px solid #ffffff;
	position: absolute;
	transform: translate(-50%, -50%);
	box-shadow: var(--default-box-shadow);
	pointer-events: none;
}

.hue-cursor {
	z-index: 1;
	left: 50%;
	width: 20px;
}

.hue-canvas {
	width: 10px;
}

.colour-picker__canvas-container {
	position: relative;
}

.hue-container {
	margin: 0 var(--block-size-base) 0 var(--block-size-lg);
}

.colour-picker__value-section+.colour-picker__value-section {
	margin-left: var(--block-size-md);
}

.colour-picker__value-section .input-field__label {
	width: var(--block-size-sm);
}

.colour-picker__value-section chip-input+chip-input {
	margin-top: var(--block-size-xs);
}

summary {
	display: list-item
}

small {
	font-size: 80%
}

[hidden] {
	display: none
}

abbr[title] {
	border-bottom: none;
	text-decoration: underline;
	text-decoration: underline dotted
}

code,
kbd,
pre,
samp,
.ff-code {
	font-family: monospace, var(--body-font-family);
}

code:not(chip-codeblock code) {
	background: var(--code-background-colour);
	color: var(--code-colour);
	padding: 2px 5px;
	border-radius: .1875rem;
	transition: color .2s, background .2s;
}

chip-codeblock pre {
	margin: 0;
	overflow: auto;
	display: flex;
}

chip-codeblock code {
	padding: 0 !important;
	display: inline-block !important;
	background: rgba(0, 0, 0, 0) !important;
}

pre {
	font-size: 1em
}

b,
strong {
	font-weight: bolder
}

sub,
sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline
}

p {
	margin-bottom: var(--block-size-lg);
}

sub {
	bottom: -.25em
}

sup {
	top: -.5em
}

iframe {
	border-style: none
}

input {
	border-radius: 0
}

input[type="text"] {
	cursor: text;
}

ol,
ul {
	margin-top: 0;
	margin-bottom: 0;
	margin-left: var(--block-size-xl);
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
	height: auto
}

[type=search] {
	-webkit-appearance: textfield;
	appearance: textfield;
}

[type=search]::-webkit-search-decoration {
	-webkit-appearance: none
}

textarea {
	overflow: auto;
	resize: vertical;
	padding: .5rem .75rem;
}

button,
input,
optgroup,
select,
textarea {
	font: inherit
}

input,
textarea {
	line-height: 1.14286;
}

optgroup {
	font-weight: var(--font-weight-bold)
}

button {
	overflow: visible;
}

button,
select {
	text-transform: none
}

[role=button],
[type=button],
[type=reset],
[type=submit],
button {
	cursor: pointer
}

[type=reset],
[type=submit],
button,
html [type=button] {
	-webkit-appearance: button;
	appearance: button;
}

:focus-visible {
	outline: var(--focus-outline);
}

chip-range input[type="range"]:focus-visible::-webkit-slider-thumb {
	outline: var(--focus-outline);
}

chip-range input[type="range"]:focus-visible::-moz-range-thumb {
	outline: var(--focus-outline);
}

select {
	-moz-appearance: none;
	-webkit-appearance: none;
	appearance: none;
}

select::-ms-expand {
	display: none
}

select::-ms-value {
	color: currentColor
}

legend {
	border: 0;
	color: inherit;
	display: table;
	max-width: 100%;
	white-space: normal
}

::-webkit-file-upload-button {
	-webkit-appearance: button;
	color: inherit;
	font: inherit
}

[disabled]:not(.list-view-item>a),
input[type="checkbox"]:not(.btn-check)[disabled]+label[for] {
	cursor: default;
	opacity: .5;
}

[disabled] {
	pointer-events: none;
}

img {
	border-style: none
}

img,
svg {
	vertical-align: middle;
}

progress {
	vertical-align: baseline
}

[aria-busy=true] {
	cursor: progress
}

[aria-controls] {
	cursor: pointer
}

[aria-disabled=true] {
	cursor: default
}

:root {
	--theme-switch-speed: .2s;

	--mark-background-colour: rgb(217, 232, 255);
	--mark-colour: rgb(29, 42, 62);

	--heading-size-1: clamp(var(--body-font-size-xl), 5.5vw, var(--body-font-size-xxl));
	--heading-size-2: clamp(var(--body-font-size-lg), 5vw, var(--body-font-size-xl));
	--heading-size-3: clamp(var(--body-font-size-md), 4.5vw, var(--body-font-size-lg));
	--heading-size-4: var(--body-font-size-md);
	--heading-size-5: 1rem;
	--heading-size-6: var(--body-font-size-sm);

	--heading-line-height: 1.175;

	--form-width: 400px;
	--link-hover-decoration: underline;

	--body-line-height-sm: 1.1429rem;
	--body-line-height: 1.4286rem;
	--body-colour: #343741;

	--form-spacing: var(--block-size-base);
	--form-spacing-lg: var(--block-size-xxl);

	--folder-colour: #A76906;

	--code-background-colour: var(--outline-colour);
	--code-colour: var(--outline-colour-foreground);

	--helper-text-font-size: var(--block-size-md);
	--helper-text-line-height: var(--block-size-base);
	--helper-text-spacing: var(--block-size-xs);
	--helper-text-padding: var(--helper-text-spacing) 0 0 0;

	--validation-error-padding: var(--block-size-xs) 0 0 0;
	--validation-error-font-size: var(--block-size-md);
	--validation-error-line-height: var(--block-size-base);
	--validation-error-font-weight: var(--font-weight-regular);

	--thumb-colour: rgb(105, 112, 125);
	--label-font-weight: var(--font-weight-semibold);

	--font-weight-light: 300;
	--font-weight-regular: 400;
