	display: inline-block;
	cursor: pointer;
	font-family: 'Font Awesome 5 Pro', var(--body-font-family);
	font-weight: 900;
	font-size: var(--body-font-size-sm);
	content: "\f054";
}

chip-treeitem[allow-open]:not(chip-tree[arrow-collapse] chip-treeitem)>.tree__toggle:before,
