chip-card[image]>*>.card-header chip-loading {
	position: absolute;
	top: 50%;
	left: 50%;
	margin: auto;
	translate: -50% -50%;
}

.loading--image {
	width: 100%;
	height: 100%;
	position: relative;
	background: var(--code-block-background-colour);
	border-radius: var(--standard-border-radius);
	overflow: hidden;
}

.loading--image:before {
	content: "";
	position: absolute;
	left: 0;
	bottom: 0;
	width: 62.5%;
	height: 62.5%;
	transform: rotate(45deg) translate(30%, 40%);
	background: rgba(var(--theme-colour-rgb), .375);
	box-shadow: calc(var(--loader-size) / 2) calc((var(--loader-size) / 2 - 2px) * -1) 0 5px var(--theme-colour);
	animation: slide 2s infinite ease-in-out alternate;
}

.loading--image:after {
	content: "";
	position: absolute;
	left: 15%;
	top: 15%;
	width: 25%;
	height: 25%;
	border-radius: 50%;
	background: var(--theme-colour);
	transform: rotate(0deg);
	transform-origin: calc(var(--loader-size) / 2) calc(var(--loader-size) * 2);
	animation: rotate 2s infinite ease-in-out;
}

@keyframes slide {

	0%,
	100% {
		bottom: -100%;
	}

	25%,
	75% {
		bottom: -2px;
	}

	20%,
	80% {
		bottom: 2px;
	}
}

@keyframes rotate {
	0% {
		transform: rotate(-15deg);
	}

	25%,
	75% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(25deg);
	}
}

chip-list[type="cards"]>ul>li:not(:first-child)>chip-listitem>chip-card {
	border-top-right-radius: 0;
	border-top-left-radius: 0;
