	--sidebar-border-width: 0;
	border: none;
	padding-right: 0;
	padding-left: var(--block-size-md);
}

/* Collapsed sidebar */
chip-framesidebar[collapsed] .sidebar-item--accordion-toggle,
chip-framesidebar[collapsed] chip-sidebaritem:has(.sidebar-item--accordion-toggle)+chip-sidebaritem {
	display: none;
}

chip-framesidebar[collapsed] chip-button[variation="sidebar"] span,
chip-framesidebar[collapsed] .sidebar-list__section-title {
	translate: calc((var(--sidebar-collapsed-width) + 100%) * -1) 0;
}

chip-framesidebar[collapsed] chip-button[variation="sidebar"] chip-icon {
	padding-left: calc(var(--btn-padding-x) + var(--block-size-sm));
}

chip-framesidebar[collapsed] .sidebar__top {
	gap: 0;
}

chip-framesidebar[collapsed] {
	width: var(--sidebar-collapsed-width) !important;
}

chip-framesidebar[collapsed]:not(:has(chip-button[variation="sidebar"][icon])) ul {
	overflow: hidden;
}

chip-framesidebar[type="curvy"] chip-button[variation="sidebar"] {
	--curve-size: var(--block-size-xxxxl);
	--btn-active-colour: var(--theme-colour);
	--btn-active-background-colour: var(--background-colour);
	--btn-padding-x: var(--block-size-lg);
	--btn-padding-y: var(--block-size-sm);
	--btn-border-colour: var(--btn-background-colour);
	--btn-line-height: 1.7143rem;
	--btn-border-radius: 100px;
	--btn-hover-background-colour: var(--theme-colour-dark);
	width: 100%;
	margin: 0;
	padding: 0;
}

chip-framesidebar[type="curvy"] chip-button[variation="sidebar"][active],
chip-framesidebar[type="curvy"] chip-button[variation="sidebar"]:active {
	--btn-border-radius: 100px 0 0 100px;
}

chip-framesidebar[type="curvy"] chip-button[variation="sidebar"][active] chip-icon,
chip-framesidebar[type="curvy"] chip-button[variation="sidebar"]:active chip-icon {
	font-weight: 900;
}

chip-framesidebar[type="curvy"] chip-sidebaritem[icon] chip-button[variation="sidebar"] a {
	padding-left: 0;
}

chip-framesidebar[type="curvy"] chip-button[variation="sidebar"][active] a,
chip-framesidebar[type="curvy"] chip-button[variation="sidebar"]:active a {
	border-width: 0;
}

chip-framesidebar[type="curvy"] chip-button[variation="sidebar"]::before,
chip-framesidebar[type="curvy"] chip-button[variation="sidebar"]::before {
	box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
	content: "";
	position: absolute;
	z-index: -1;
	transition: box-shadow var(--theme-switch-speed);
	top: calc(var(--curve-size) * -1);
	right: 0;
	height: var(--curve-size);
	width: min(var(--curve-size), 70%);
	border-bottom-right-radius: 50%;
	pointer-events: none;
	background: rgba(0, 0, 0, 0);
}

chip-framesidebar[type="curvy"] chip-button[variation="sidebar"][active]::before,
chip-framesidebar[type="curvy"] chip-button[variation="sidebar"]:active::before {
	box-shadow: 0 calc(var(--curve-size) / 2) 0 0 var(--btn-active-background-colour);
}

chip-framesidebar[type="curvy"] chip-button[variation="sidebar"]:not(.sidebar-list-item:last-child chip-button)[active]::after,
chip-framesidebar[type="curvy"] chip-button[variation="sidebar"]:not(.sidebar-list-item:last-child chip-button):active::after {
	box-shadow: 0 calc(var(--curve-size)/2*-1) 0 0 var(--btn-active-background-colour);
}

chip-framesidebar[type="curvy"] chip-button[variation="sidebar"]:not(.sidebar-list-item:last-child chip-button)::after,
chip-framesidebar[type="curvy"] chip-button[variation="sidebar"]:not(.sidebar-list-item:last-child chip-button)::after {
	content: "";
	position: absolute;
	bottom: calc(var(--curve-size) * -1);
	right: 0;
	pointer-events: none;
	transition: box-shadow var(--theme-switch-speed);
	box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
	height: var(--curve-size);
	width: min(var(--curve-size), 70%);
	border-top-right-radius: 50%;
	z-index: -1;
	background: rgba(0, 0, 0, 0);
}

chip-frame[sidebar-on-top] chip-framesidebar {
	top: 0;
}

